.hero-header {
  background-color: var(--primal-color);
  padding-top: 65px;
}

.hero {
  background-color: var(--primal-color);
  background-repeat: no-repeat;
  position: relative;
}

.hero-title {
  width: 247px;
  text-align: left;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--focus-color);
