}

.products-btn {
  text-align: center;
  color: #131313;
  border: 2px solid var(--second-color);
  transition: border-color var(--transition-dur-func);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 11px 26px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  animation: animate105 3s 1s infinite;
  display: flex;
  overflow: hidden;
}

@keyframes animate105 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.products-btn:hover, .products-btn:focus {
  border-color: var(--hover-color);
}

.products-btn:before {
  content: "";
  width: 1em;
  height: 100%;
  background-color: rgba(248, 210, 78, .05);
  text-decoration: none;
  animation: shine 2s infinite;
  position: absolute;
  top: 0;
  left: 0;
  transform: skewX(-45deg);
}

.products-btn:hover:before {
  background-color: rgba(10, 136, 23, .05);
}

@keyframes shine {
  from {
    transform: skewX(-45deg);
  }

  to {
    transform: translateX(10em)skewX(-45deg);
  }
}

.btn-link {
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.made-link {
  text-align: center;
  color: #f8d24e;
